.ProjectList {
  margin: 0 auto;
}
.ProjectListCarousel {
  text-align: center;
  margin: 0 auto;
}

.ProjectListTitleFavorite {
  font-size: large;
  font-weight: 600;
}


@media only screen and (max-device-width: 800px) {
  .ProjectListItem {
    font-size: small;
  }
  .ProjectListItem td {
    padding: 8px;
  }
  .ProjectListTitleFavorite {
    font-size: small;
  }
}
